body.active-modal {
  overflow:hidden;
}

.btn-modal {
  padding: 10px 20px;
  display: block;
  margin: 100px auto 0;
  font-size: 18px;
}

.modal,
.overlay {
  width: 100vw;
  height: 100vh;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  right: 0;
  bottom: 0;
  position: fixed;
  z-index: 100;
}

.overlay {
   background-color: rgba(0,0,0,0.25);
  z-index: 100;
}
.modal-content {
  position: absolute;
  top: 5px;
  left: 50%;
  transform: translateX(-50%);
  line-height: 0.4;
  background-color: white;
  padding: 14px 28px;
  border-radius: 15px;
  z-index: 100;
  width: 512px;
  height: 700px;
}

.close-modal {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 5px 7px;
}
div input:focus {
  outline: none;
}
@media ((min-width: 1025px) and (max-width : 1151px)) {
  .modal-content {
    left: 60%;
  }
}
